<template>
  <div id="app">
    <!-- 头部 -->
    <app-header :poiInfo="poiInfo"></app-header>
    <!-- 导航 -->
    <div class="nav">
      <app-nav></app-nav>
    </div>
    <!-- 内容 -->
    <router-view></router-view>
  </div>
</template>

<script>
  import Header from './components/header/Header'
  import Nav from './components/nav/Nav'
  // import Header from './components/header/Header'

export default {
  name: 'App',
  components:{
    "app-header":Header,
    "app-nav":Nav,
  },
  data(){
    return {
      poiInfo:{}
    }
  },
  created() {
    fetch("/api/goods")
      .then(res=>{
        return res.json()
      })
      .then(response =>{
        if(response.code == 0){
          this.poiInfo = response.data.poi_info
        }
      })
  },
}
</script>

<style>

</style>



